<template>
  <div class="cash-register">
    <van-nav-bar
      title="会员注册"
      left-text="返回"
            :fixed="true"
      left-arrow
      @click-left="onClickLeft"
    ></van-nav-bar>
    <div class="zhan"></div>
    <van-cell-group>
      <van-field v-model="form.memberName" placeholder="请输入" label="姓名" />
      <van-field
        v-model="form.memberNo"
        type="number"
        placeholder="请输入"
        label="手机"
      />
      <van-field name="radio" label="性别">
        <template #input>
          <van-radio-group v-model="form.sex" direction="horizontal">
            <van-radio :name="1">男</van-radio>
            <van-radio :name="2">女</van-radio>
          </van-radio-group>
        </template>
      </van-field>
    </van-cell-group>

    <van-cell-group>
      <van-field
        v-model="form.money"
        type="number"
        placeholder="请输入"
        label="充值金额"
      />
      <van-field
        v-model="form.extraMoney"
        type="number"
        placeholder="请输入"
        label="赠送金额"
      />

      <!-- <van-field name="checkboxGroup" label="付款方式">
        <template #input>
          <van-checkbox-group v-model="payType" direction="horizontal">
            <van-checkbox
              v-for="item in paytypes"
              :key="item.payType"
              :name="item.payType"
              shape="square"
              >{{ item.payTypeName }}</van-checkbox
            >
          </van-checkbox-group>
        </template>
      </van-field> -->

      <van-field name="radio" label="付款方式">
        <template #input>
          <van-radio-group v-model="form.payType" direction="horizontal">
            <van-radio
              v-for="item in paytypes"
              :key="item.payType"
              :name="item.payType"
              >{{ item.payTypeName }}</van-radio
            >
          </van-radio-group>
        </template>
      </van-field>
    </van-cell-group>

    <van-cell-group>
      <van-field
        v-model="form.remark"
        rows="2"
        autosize
        label="备注"
        type="textarea"
        maxlength="200"
        placeholder="请输入"
        show-word-limit
      />
    </van-cell-group>

    <div class="btn-wrap">
      <van-button type="info" block @click="submitHandler">提交</van-button>
    </div>
  </div>
</template>

<script>
import { memberAdd } from '@/api/index';
import { mapState } from 'vuex';

import { checkMobile } from '@/utils/index';

export default {
  name: 'MemberRegister',
  components: {},
  data() {
    return {
      payType: [],
      form: {
        memberName: '',
        memberNo: '',
        sex: 1,
        money: '',
        extraMoney: '',
        payType: '',
        remarks: ''
      }
    };
  },
  watch: {
    payType(val) {
      this.form.payType = val.join();
    }
  },
  computed: {
    ...mapState(['paytypes', 'userInfo'])
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    submitHandler() {
      if (!this.form.memberName) {
        this.$Toast('请输入姓名');
        return;
      }

      if (checkMobile(this.form.memberNo).code) {
        this.$Toast('请输入电话号码');
        return;
      }

      if (!this.form.money) {
        this.$Toast('请输入充值金额');
        return;
      }

      if (!this.form.payType) {
        this.$Toast('请选择付款方式');
        return;
      }

      memberAdd({
        ...this.form,
        phone: this.form.memberNo,
        createBy: this.userInfo.loginName
      }).then(res => {
        if (res) {
          if (res.data.code === 0) {
            // todo
            this.$Toast('会员注册成功');
            this.$router.go(-1);
          } else {
            this.$Toast(res.data.msg);
          }
        } else {
          this.$Toast('注册失败');
        }
      });
    }
  }
};
</script>


