    margin-top: 12px;
  }

  .van-radio,
  .van-checkbox {
    margin: 5px 8px;
  }

  .btn-wrap {
    width: 90%;
    margin: 10px auto;

    .van-button {
      height: 40px;
      border-radius: 18px;
    }
  }

  .van-cell__value {
    text-align: center;
  }
}
</style>
